<template>
  <div class="wrap" v-if="initVisible">
    <button class="arr left" @click="clickLeft"></button>
    <button class="arr right" @click="clickRight"></button>
    <div class="scroll-list">
      <ul class="box-wrap" ref="refScrollWrap">
        <li v-for="(exItem, idx) in experienceList" :key="idx" @click="contentPopOpen(exItem)">
          <div class="img-wrap">
            <img :src="exItem.img" />
          </div>
          <div class="title-wrap">
            <p class="title">{{ exItem.title }}</p>
            <p class="title">{{ exItem.date }}</p>
            <ul class="skill-wrap">
              <li :style="`background-color: ${randomColor()}`" v-for="(subExItem, subIdx) in exItem.useItem" :key="subIdx">{{ subExItem }}</li>
            </ul>
          </div>
        </li>
      </ul>
    </div>

    <div class="overlay" v-if="contentPopVisible">
      <div class="popup-wrap">
        <div class="popup-header">
          <button @click="contentPopClose">
            <i class="fas fa-times"></i>
          </button>
        </div>
        <div class="popup-body">
          <div class="bg-line">
            <div class="img-wrap">
              <img :style="`background-image: url(${popDetail.img})`">
            </div>
            <div class="mb10">
              <h4>{{ popDetail.title }}</h4>
              <span class="sub-title pl6 mb4">({{ popDetail.date }})</span>
              <p class="sub-title pl10">{{ popDetail.role }}</p>
            </div>
            <div>
              <h5 class="mb4">소개</h5>
              <p class="title mb6 pl10" v-for="(data, dataIdx) in popDetail.description" :key="dataIdx">{{ data }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, onUnmounted } from 'vue';
import gw from '@/assets/images/experience/gw.jpg'
import wtm from '@/assets/images/experience/wtm2.jpg'
import ipm from '@/assets/images/experience/ipm.jpg'
import esm from '@/assets/images/experience/esm2.jpg'
import pdm from '@/assets/images/experience/pdm2.jpg'
import rhm from '@/assets/images/experience/rhm2.jpg'
import oam from '@/assets/images/experience/oam3.jpg'
import sam from '@/assets/images/experience/sam3.jpg'
import home from '@/assets/images/experience/home.jpg'
import msg from '@/assets/images/experience/msg2.png'
import wtm78 from '@/assets/images/experience/78wtm.jpg'

export default {
  setup() {
    const experienceList = [
      {
        title: '사내 그룹 웨어',
        date: '2022.10 ~ 2023.05',
        img: gw,
        role: '프론트엔드 개발',
        useItem: [
          'Vue3', 'TypeScript', 'SCSS', 'graphQL', 'pinia'
        ],
        description: [
          '업무 관리, 근태 관리, 세일즈 관리를 할 수 있는 사내망 페이지',
          'Vue3의 안정화 버전이 정식 릴리즈 되면서 공식 문서 또한 Vue3로 대체됨에 따라 개선된 프레임워크를 이용해보고자 Vue3를 메인 프레임워크로 개발한 프로젝트',
        ],
      },
      {
        title: 'WTM8.0(근무시간 관리)',
        date: '2022.11 ~ 2023.05',
        img: wtm,
        role: '프론트엔드 개발',
        useItem: [
          'Vue2', 'CSS', 'graphQL', 'VueX', 'DevExpress'
        ],
        description: [
          '근무 환경을 효율적으로 관리하기 위한 솔루션',
          '이 솔루션의 특이점은 캘린더를 사용하며, 일단위/주단위/월단위로 근로자들의 근무시간 현황을 파악할 수 있다는 점입니다.'
        ],
      },
      {
        title: 'IPM(네트워크 관리)',
        date: '2022.06 ~ 2022.11',
        img: ipm,
        role: '프론트엔드 개발',
        useItem: [
          'Vue2', 'CSS', 'graphQL', 'VueX', 'DevExpress'
        ],
        description: [
          'IP현황을 파악하여 관리 및 통제를 하는 솔루션',
          '이 솔루션의 특이점은 IP의 할당 및 사용 상태를 한눈에 파악 할 수 있는 IP 매트릭스입니다.',
        ],
      },
      {
        title: 'ESM(보안 관리)',
        date: '2022.01 ~ 2022.05',
        img: esm,
        role: '프론트엔드 개발',
        useItem: [
          'Vue2', 'CSS', 'graphQL', 'VueX', 'DevExpress'
        ],
        description: [
          '기업 정보가 유출되지 않도록 사전에 감시 및 통제를 하는 솔루션',
          '관리자가 다양한 보안 정책을 설정할 수 있는 관리자 웹 서비스를 개발하였습니다.'
        ],
      },
      {
        title: 'PDM(패치&배포 관리)',
        date: '2021.08 ~ 2021.12',
        img: pdm,
        role: '프론트엔드 개발',
        useItem: [
          'Vue2', 'CSS', 'graphQL', 'VueX', 'DevExpress'
        ],
        description: [
          '설정한 정책을 통해 사용자PC 업데이트를 제어 및 자동 배포하는 솔루션',
          '관리자가 지정한 대상에게 특정시점/특정 기간내에 패키지 리스트를 배포할 수 있도록 정책을 제공하는 웹 서비스 입니다.',
        ],
      },
      {
        title: 'RHM(원격 장애 관리)',
        date: '2021.04 ~ 2021.08',
        img: rhm,
        role: '프론트엔드 개발',
        useItem: [
          'Vue2', 'CSS', 'graphQL', 'VueX', 'DevExpress'
        ],
        description: [
          '장애 처리의 편의성을 높이기 위해 작업 단계별 상황을 모니터링할 수 있는 솔루션',
          '지라와 유사한 프로젝트로, 이슈를 등록하면 담당자가 작업을 배정하여 이슈를 관리하는 형태의 웹 서비스입니다.',
        ],
      },
      {
        title: 'SAM(소프트웨어 자산 관리)',
        img: sam,
        date: '2020.12 ~ 2021.04',
        role: '프론트엔드 개발',
        useItem: [
          'Vue2', 'CSS', 'graphQL', 'VueX', 'DevExpress'
        ],
        description: [
          '계약한 소프트웨어를 등록하여 한눈에 현황을 파악하고, 고유한 규칙을 정의하여 관리하는 솔루션',
          '소프트웨어의 사용 이력, 접속 이력등을 확인 할 수 있으며 설치 현황, 통제 현황등을 보여줍니다.',
        ],
      },
      {
        title: 'OAM(실물 자산 관리)',
        date: '2020.08 ~ 2020.12',
        img: oam,
        role: '프론트엔드 개발',
        useItem: [
          'Vue2', 'CSS', 'graphQL', 'VueX', 'DevExpress'
        ],
        description: [
          '자산 상태를 실시간으로 파악하여 자산현황을 확인할 수 있는 솔루션',
          '거래처와 관리자, 품목등을 분류 및 등록하여 계약 및 관리 현황을 한 눈에 볼 수 있습니다.',
        ],
      },
      {
        title: '자사 홈페이지 리뉴얼',
        date: '2022.11 ~ 2023.01',
        img: home,
        role: '퍼블리싱',
        useItem: [
          'PHP', 'jQuery', 'gnuboard', 'cafe24'
        ],
        description: [
          '모바일 페이지 부재 및 디자인 트렌드 변화를 반영하기 위해 리뉴얼 진행',
          '반응형 페이지로 제작되었습니다.',
        ],
      },
      {
        title: 'NetHelper Messenger',
        date: '2020.06 ~ 2020.07',
        img: msg,
        role: '퍼블리싱',
        useItem: [
          'Vue2', 'SCSS', 'Vuetify'
        ],
        description: [
          '업무용 메신저',
          'Electron 기반의 크로스 플랫폼 데스크톱 앱 프로젝트',
        ],
      },
      {
        title: 'WTM7.8 (근무시간 관리)',
        date: '2020.04 ~ 2023.05',
        img: wtm78,
        role: '퍼블리싱',
        useItem: [
          'HTML', 'CSS', 'jQuery', 'TOAST UI', 'FullCalendar'
        ],
        description: [
          '근무 환경을 효율적으로 관리하기 위한 솔루션',
          'PC 웹 페이지 유지보수 및 고객사 커스텀을 담당하였습니다.',
          '모바일 페이지 부재로 인해 모바일 페이지 퍼블리싱을 진행하였습니다.',
        ],
      },
    ];
    
    const refScrollWrap = ref();

    // **** component visible ****
    const initVisible = ref(false);
    const openComponent = () => {
      initVisible.value = true;
    };
    const closeComponent = () => {
      initVisible.value = false;
    };

    // **** 랜덤 컬러 ****
    const randomColor = () => {
      let color_r = Math.floor(Math.random() * 127 + 128).toString(16);
      let color_g = Math.floor(Math.random() * 127 + 128).toString(16);
      let color_b = Math.floor(Math.random() * 127 + 128).toString(16);
      return `#${color_r+color_g+color_b}`;
    }

    //  **** 휠 이벤트 ****
    let progress = 0;
    let active = 0;
    let speedWheel = 0.1;

    const handleWheel = e => {
      const wheelProgress = e.deltaY * speedWheel
      progress = Math.max(0, Math.min(progress + wheelProgress, 100));
      active = Math.floor(progress/100*(experienceList.length-1));
      if(refScrollWrap.value && refScrollWrap.value.style) refScrollWrap.value.style.setProperty('--active', `-${active}`);
    };

    //  **** 클릭 이벤트 ****
    const clickLeft = () => {
      if(active === 0) return;
      progress -= 20;
      active -= 1;
      refScrollWrap.value.style.setProperty('--active', `-${active}`);
    };

    const clickRight = () => {
      if(active === experienceList.length-1) return;
      progress += 20;
      active += 1;
      refScrollWrap.value.style.setProperty('--active', `-${active}`);
    };

    onMounted(() => {
      document.addEventListener('mousewheel', handleWheel);
    });

    onUnmounted(() => {
      document.removeEventListener('mousewheel', handleWheel);
    });

    // **** popup *****
    let popDetail = ref({});
    const contentPopVisible = ref(false);
    const contentPopOpen = (item) => {
      popDetail.value = item;
      contentPopVisible.value = true;
    };
    const contentPopClose = () => {
      popDetail.value = {};
      contentPopVisible.value = false;
    };

    return {
      experienceList,
      refScrollWrap,
      initVisible,
      openComponent,
      closeComponent,
      randomColor,
      clickLeft,
      clickRight,
      popDetail,
      contentPopVisible,
      contentPopOpen,
      contentPopClose,
    };
  }
}
</script>